import React, { useState, useEffect } from "react";
import "../styles/Login.css"
import { Link, useHistory } from 'react-router-dom'
import logo from "../images/logoBlack.svg"
import { useAuth } from "../context/userContext"; // context

import { ButtonLarge } from '../components/utilities/Buttons'

const Login = () => {

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [error, setError] = useState("");
    const [windowsWidth, setWindowsWidth] = useState(window.innerWidth)

    const { login, signInWithGoogle } = useAuth() // context
    const history = useHistory()


    function isMobile() {
        if (windowsWidth > 1100) {
            return false
        }
        else {
            return true
        }
    }

    function handleResize(width) {
        setWindowsWidth(width)
    }


    useEffect(() => {
        window.addEventListener("resize", (e) => handleResize(window.innerWidth))
    }, [])



    async function handleLoginForm(e) {
        e.preventDefault()
        try {
            const res = await login(email, password)
            if (res === false) {
                setPassword("")
                setError("Merci de confirmer votre adresse email.")
            }
            else {
                history.push('/dashboard')
            }
        } catch {
            setPassword("")
            setError("Nous n'avons pas russi  vous identifier, veuillez ressayer.")
        }
    }


    return (
        <div className={!isMobile() ? "loginSubscribeContainer" : "loginSubscribeContainer mobile"}>
            <header>
                <div className="logoContainer">
                    <img alt="" src={logo} />
                </div>
                <div className="blocLoginTopRight">
                    <Link to="/"><img alt="" src={'/images/backArrow.png'} />Revenir  l'accueil</Link>
                </div>
            </header>
            <div className={!isMobile() ? "loginPageContainer" : "loginPageContainer mobile"}>
                <div className="loginContainer">
                    <h1>Connectez-vous  Nivid</h1>
                    <div className="googleSignInContainer">
                        <buttton className="googleSignInButton" onClick={() => signInWithGoogle()}>
                            <div className="googleSignInButtonImageContainer">
                                <img alt="" src={'/images/google.png'} />
                            </div>
                            <span>Connectez-vous avec Google</span>
                        </buttton>
                    </div>
                    <div className="LoginDiviserContainer">
                        <div className="LoginDiviser">
                            <p>ou</p>
                        </div>
                    </div>
                    {error && (
                        <div className="errorMessage">
                            {error}
                        </div>
                    )}
                    <form>
                        <div className="formLine">
                            <div className="formField">
                                <label>Identifiant / Adresse email</label>
                                <input value={email} type="email" name="email" onChange={(e) => setEmail(e.target.value)} />
                            </div>
                        </div>

                        <div className="formLine">
                            <div className="formField">
                                <label>Mot de passe<span onClick={() => history.push("/mot-de-passe-oublie")}>Mot de passe oubli ?</span></label>
                                <input value={password} type="password" name="password" onChange={(e) => setPassword(e.target.value)} />
                            </div>
                        </div>

                        <div className="formLine">
                            <div className="formField">
                                <ButtonLarge
                                    onClick={(e) => handleLoginForm(e)}
                                    color="orange"
                                    value="Cest parti !"
                                    backgroundColor="rgba(245, 192, 67)"
                                    marginTop="10px"
                                />
                            </div>
                        </div>

                        <div className="formLine">
                            <div className="formField loginBottomBloc">
                                <p>Pas encore de compte ?</p>
                                <Link to="/inscription">Inscrivez-vous</Link>
                            </div>
                        </div>
                    </form>
                </div>
            </div>

        </div>
    )
}
export default Login;
